export default {
	data() {
		return {
			isSelect:false,
			addressList:[
				{
					id:1,
					name:"大黑哥",
					head:"大",
					tel:"18816881688",
					address:{
						region:{
							"label":"广东省-深圳市-福田区",
							"value":[18,2,1],"cityCode":"440304",
							},
							detailed:'深南大道1111号无名摩登大厦6楼A2',
							},isDefault:true},
				{
					id:2,
					name:"大黑哥",
					head:"大",
					tel:"15812341234",
					address:{
						region:{
							"label":"广东省-深圳市-福田区",
							"value":[18,2,1],"cityCode":"440304",
							},
							detailed:'深北小道2222号有名公寓502',
							},
							isDefault:false,
							},
				{
					id:3,
					name:"老大哥",
					head:"老",
					tel:"18155467897",
					address:{
						region:{
							"label":"广东省-深圳市-福田区",
							"value":[18,2,1],
							"cityCode":"440304",
							},
							detailed:'深南大道1111号无名摩登大厦6楼A2',
							},
							isDefault:false,
							},
				{
					id:4,
					name:"小黑妹",
					head:"王",
					tel:"13425654895",
					address:{
						region:{
							"label":"广东省-深圳市-福田区",
							"value":[18,2,1],
							"cityCode":"440304",
							},
							detailed:'深南大道1111号无名摩登大厦6楼A2',
							},
							isDefault:false,
							},
			]
		};
	},
	onShow() {		
		uni.getStorage({
			key:'delAddress',
			success: (e) => {
				let len = this.addressList.length;
				if(e.data.hasOwnProperty('id')){
					for(let i=0;i<len;i++){
						if(this.addressList[i].id==e.data.id){
							this.addressList.splice(i,1);
							break;
						}
					}
				}
				uni.removeStorage({
					key:'delAddress'
				})
			}
		})
		uni.getStorage({
			key:'saveAddress',
			success: (e) => {
				let len = this.addressList.length;
				if(e.data.hasOwnProperty('id')){
					for(let i=0;i<len;i++){
						if(this.addressList[i].id==e.data.id){
							this.addressList.splice(i,1,e.data);
							break;
						}
					}
				}else{
					let lastid = this.addressList[len-1];
					lastid++;
					e.data.id = lastid;
					this.addressList.push(e.data);
				}
				uni.removeStorage({
					key:'saveAddress'
				})
			}
		})
	},
	onLoad(e) {
		if(e.type=='select'){
			this.isSelect = true;
		}
	},
	methods:{
		edit(row){
			uni.setStorage({
				key:'address',
				data:row,
				success() {
					uni.navigateTo({
						url:"edit/edit?type=edit"
					})
				}
			});				
		},
		add(){
			uni.navigateTo({
				url:"edit/edit?type=add"
			})
		},
		select(row){
			//是否需要返回地址(从订单确认页跳过来选收货地址)
			if(!this.isSelect){
				return ;
			}
			uni.setStorage({
				key:'selectAddress',
				data:row,
				success() {
					uni.navigateBack();
				}
			})
		}
	}
}
